import createFetchService from "../services/createFetchService.js";
import { createLogger } from "../helpers/createLogger.js";
import { useSanctumOptions } from "./useSanctumOptions.js";
export const useSanctumFetch = (request, options) => {
  const { logLevel } = useSanctumOptions();
  const logger = createLogger(logLevel);
  const { onRequest, onResponseError, ...otherOptions } = options || {};
  const fetchServiceOptions = {
    ...onRequest ? { onRequest } : {},
    ...onResponseError ? { onResponseError } : {}
  };
  const fetchService = createFetchService(fetchServiceOptions, logger);
  return fetchService(request, otherOptions);
};
