import createFetchService from "./services/createFetchService.js";
import { useCurrentUser } from "./composables/useCurrentUser.js";
import { createLogger } from "./helpers/createLogger.js";
import { useSanctumOptions } from "./composables/useSanctumOptions.js";
import { getAuthUser } from "./helpers/get-auth-user.js";
import { defineNuxtPlugin } from "#app";
export default defineNuxtPlugin(async () => {
  const options = useSanctumOptions();
  const user = useCurrentUser();
  const logger = createLogger(options.logLevel);
  const fetchService = createFetchService({}, logger);
  if (!user.value) {
    try {
      user.value = await getAuthUser(fetchService);
    } catch (error) {
      logger.debug("Failed to fetch authenticated user:", error);
    }
  }
  return {
    provide: {
      sanctumFetch: fetchService
    }
  };
});
